@import url(https://fonts.googleapis.com/css?family=Zilla+Slab&display=swap);
@import url(https://fonts.googleapis.com/css?family=Zilla+Slab&display=swap);
body {
  margin: 0;
  font-family: 'Zilla Slab', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}
@media (max-width: 1024px){
  body, html {
  /* height: 100%!important; */
  overflow: hidden!important;
  max-height: 100% !important;
  }

}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

footer {
  margin-top: 0px;
  padding-top: 0px;
}
#root {
  height: 100vh;
}
.wrapper {
    overflow: clip;
    display: flex;
    justify-content: flex-start;
    flex-flow: column nowrap;
    align-content: space-a;
    margin: auto;
    text-align: center;
    width: 100%;
    height: 100%;
    /* min-height: 450px; */
    background-image: url(/friend-adventure/static/media/paper_fibers.d1121140.png);
    background-repeat: round;
    background-color: rgb(209, 223, 199);
}


button {
    font-family: 'Zilla Slab','Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    position: relative;
    width: 15rem;
    margin: 1rem auto;
    height: 2.2rem;
    -webkit-transition-duration: .5s;
            transition-duration: .5s;
    border: 2px solid black;
    border-radius: 10px;
    -webkit-animation: fade-in 1.2s forwards;
            animation: fade-in 1.2s forwards;
    background-color: rgb(212, 212, 212);
}

button:hover, button:focus {
    cursor: pointer;
    color: whitesmoke;
    background-color: rgb(11, 54, 75);
    -webkit-transition-duration: .5s;
            transition-duration: .5s;
}

.choice-container {
    display: flex;
    flex-flow: column nowrap;
    align-content: center;
    margin: auto;
    background-color: rgb(247, 247, 247);
    box-shadow: black 0px 0px 15px;
    position: fixed;
    width: 100%;
    padding-top: 50px;
    height: 0;
    z-index: 10;
    opacity: 0;
    bottom: -50px;
    -webkit-animation: show-container 1s ease-out forwards;
            animation: show-container 1s ease-out forwards;
    -moz-animation: show-container 1s ease-out forwards;
}

.choice-arrow {
    pointer-events: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -moz-user-select: none;
         user-select: none;
    -webkit-transition: .3s;
    transition: .3s;
}
.choice-arrow-container {
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
}

.choice-arrow-container:hover {
    -webkit-transition: background-color .25s ease-in;
    transition: background-color .25s ease-in;
    background-color: rgb(11, 54, 75);
}
.choice-arrow-container:hover > .choice-arrow {
    -webkit-transform: rotate(.5turn);
            transform: rotate(.5turn);
    -webkit-transition: -webkit-transform .3s;
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
    
}

.button-container.hidden {
    display: none;
    margin: 1rem;
}
@-webkit-keyframes show-container {
    0% {
        opacity: 0;
        bottom: -50px;
    } 
    100% {
        opacity: 1;
        bottom: 0;
    }
}
@keyframes show-container {
    0% {
        opacity: 0;
        bottom: -50px;
    } 
    100% {
        opacity: 1;
        bottom: 0;
    }
}

.choice-container.show {
    height: auto;
}
.choice-container.show > .button-container {
    display: flex;
    flex-flow: row wrap;
    width: 80%;
    max-width: 1100px;
    margin: auto;
    -webkit-transition: 1s;
    transition: 1s;
}
.choice-container.show > .choice-arrow-container {
    background-color: rgb(11, 54, 75);
}
.choice-container.show > .choice-arrow-container > .choice-arrow {
    -webkit-transform: rotate(.5turn);
            transform: rotate(.5turn);
}

@-webkit-keyframes fade-in{
    0% {
        opacity: 0;
    } 
    100% {
        opacity: 1;
    }
}

@keyframes fade-in{
    0% {
        opacity: 0;
    } 
    100% {
        opacity: 1;
    }
}

.game-content {
    padding-top: 2rem;
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    width: 100%;
    background-image: url(/friend-adventure/static/media/paper_fibers.d1121140.png);
    background-repeat: round;
    background-color: rgb(209, 223, 199);
    height: 100%;
}

.game-load {
    opacity: 1;
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
    
}

.game-story {
    text-align: left;
    opacity: 0;
    height: 80%;
    margin:  auto;
    padding: 1rem;
    padding-bottom: 6rem;
    max-height: 500px;
    width: 95%;
    max-width: 800px;
    box-shadow: black 7px 7px 12px;
    background-color: rgba(255, 248, 220, 0.26);
    overflow-y: auto;
    -webkit-animation: fade-in 1s forwards;
            animation: fade-in 1s forwards;
}
.game-story > p {
    text-indent: 1rem;
}
.game-story > h2 {
    text-align: center;
}
.choice-area-prompt {
    display: flex;
}
.choice-area-dialogue {
    -webkit-animation: fade-in 1s forwards;
            animation: fade-in 1s forwards;
}

.game-story::-webkit-scrollbar {
    display: auto;
    width: 10px;
    background-color: rgb(217, 215, 226);
}
.game-story::-webkit-scrollbar-thumb {
    background: rgb(2, 0, 22);
}
.game-story >  * > button {
    margin: 1;
    justify-self: center;
}


input {
    height: 2rem;
    font-size: 1.8rem;
    width: 20rem;
    margin: 0 auto;
    border-radius: 8px;
}

.title {
    position: relative;
    

}



.footer {
    position: absolute;
    bottom: 0;
    height: 2.5rem;
    box-shadow: darkslategrey 0px 0px 8px;
    opacity: 0;
    width: 100%;
    -webkit-animation: fade-in 1.2s ease-in 1.8s forwards;
            animation: fade-in 1.2s ease-in 1.8s forwards;
}


form {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-evenly;
    align-content: space-around;
    height: 20rem;
    box-shadow: rgb(57, 77, 55) 3px 3px 18px;
    -webkit-transition-duration: 1.2s;
            transition-duration: 1.2s;
    width: 100%;
    margin: 2rem auto;
}

form:hover, form:focus {
    background-color: rgb(53, 132, 235);
    -webkit-transition-duration: 1.2s;
            transition-duration: 1.2s;
    box-shadow: rgb(196, 184, 20) 3px 3px 60px;

}
div.warning::before {
    position: absolute;
    content: "A Richard already exists in this story";
    background-color: yellow;
    top: -1.3rem;   
}

input.warning{
    background-color: rgb(253, 4, 4);
    color: white;
}

div {
    position: relative;
}

.continue {
    display: none;
    opacity: 0;
    -webkit-transition: opacity 0s;
    transition: opacity 0s;
    position: absolute;
    width: 6rem;
    bottom: -.5rem;
    left: 52%;
    right: 48%;
}

.continue.show {
    display: block;
    opacity: 1;
    -webkit-transition: opacity .4s;
    transition: opacity .4s;
}

form {
    position: relative;
}
header {
    position: fixed;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-content: center;
    text-align: center;
    width: 100%;
    height: 4rem;
    background-color: rgb(7, 17, 26);
    color: cornsilk;
}

.root {
  height: 100vh;
}
